.navbar-nav a:hover, a:visited, a:active {
    border-bottom: 2px solid cyan;
}

.navbar-brand a:hover, a:visited, a:active {
    border-bottom: none;
}

.nameContainer {
    text-align: center;
}

.name {
    font-size: 42px;
}

.jobTitle {
    margin-top: -6px;
    font-size: 18px;
}

.nav-link {
    font-size: 24px;
    text-align: center;
    padding: 0;
}

@media only screen and (max-width: 1000px) {
    .navContainer {
        margin: 10px 0 10px;
    }

    .nav-link {
        width: 80px;
        margin: auto;
    }
}

@media only screen and (min-width: 1000px) {
    .navContainer {
        margin: 50px 0 50px;
    }

    .nav-link {
        width: auto;
        margin: auto 10px;
    }
}
